/* Extra: navbar  */
body nav { 
	width: 100%; min-height: 3rem;
	background-color: var(--background); 
	border-bottom: solid 0.1rem var(--border); 
}
body > nav { position: fixed; top: 0; left: 0; }
nav ul:first-child { 
	list-style-type: none;
	margin: auto; 
	max-width: 50rem;
	padding: 0 0.5rem;
	overflow: visible;
}
nav ul:first-child > li { 
	display: inline-block;
	margin: 0;
	padding: 0.8rem 1.0rem;
	position: relative;
	float: left; 
}
nav ul:first-child > li:first-child { padding-left: 0; }
nav ul > li > ul { 
	display: none; 
	width: auto;
	position: absolute;
	padding: 1.0rem 2.5rem 0rem;
	background-color: var(--background);
	z-index: 2;
}
nav ul > li > ul > li { white-space: nowrap; } 
nav ul > li:hover > ul { display: block; }
nav+main, nav+header { margin-top: 3rem; }

nav img {
  margin: 0px !important;
  justify-content: center;
}

nav a {
  justify-content: center;
}

nav ul li a {
  text-decoration: none !important;
}
